import { Injectable, ElementRef } from '@angular/core';


@Injectable()
export class PopBoxOptions {

    /**
     *  box 
     */
    target?: ElementRef | any;
    /**
     *  target , top, left, bottom, right,
     */
    position?: {
        top: number,
        left: number
    };
    /**
     * Closes the modal when escape key is pressed.
     */
    keyboardESCClose?: boolean;
    /**
     * Css class for opened modal
     */
    class?: string;
    /**
     *  data
     */
    initialState?: Object;
    /**
     * Placement of a pop-box
     * Default: "bottom left"
     * Accepts:
     * "top", "top left", "top right",
     * "bottom", "bottom left", "bottom right",
     * "left", "left top", "left bottom",
     * "right", "right top", "right bottom".
     */
    placement?: string;

    offset?: number;

    /**
     *  arrow
     */
    arrow?: boolean;

    /**
     * 
     */
    outsideAutoClose?: boolean;

    /**
     * 
     */
    insideAutoClose?: boolean;

    // 
    stopPropagation?: boolean;

    zIndex?: number;

    //  body
    append?: string;

    // 
    showMask?: boolean;

    // 
    autoClosePrevious?: boolean;

    //  target 
    openedClass?: string;

    //  target 
    containerClass?: string;

    // 
    autoAdapt?: boolean;
}

export const popBoxConfigDefaults: PopBoxOptions = {
    placement: 'bottom center',
    arrow: false,
    keyboardESCClose: true,
    outsideAutoClose: true,
    insideAutoClose: false,
    offset: 0,
    stopPropagation: false,
    append: 'body',
    showMask: false,
    autoClosePrevious: false,
    openedClass: 'thy-pop-box-opened',
    autoAdapt: true
};
